/* style for the tables*/

/* style for tableWithBorders */
.tableWithBorders {
    border-collapse: collapse;
    width: 100%;
}

/* style for tableWithBorders th, tableWithBorders td */
.tableWithBorders th, .tableWithBorders td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

/* style to make the first row not bolder */
.tableWithBorders tr:first-child th, .tableWithBorders tr:first-child td {
    font-weight: normal;
}
